@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #232F3E;
$SECONDARY_COLOR: #37475A;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #1995D3;
$SECONDARY_THEME_COLOR: #1AB744;
$PRIMARY_BG_COLOR: #F5F6F9;
$TRANSITION: all ease-in-out 0.3s;

.main-products-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: $PRIMARY_BG_COLOR;
    gap: 0.5rem;
    padding: 0.5rem;
    font-family: $PRIMARY_FONT;
    z-index: 1;
}

.single-card{
    width: 16rem;
    padding: 20px;
    display: flex;
    flex-direction: column;
    gap: 8px;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.single-card:hover{
    text-decoration: none;
}

.product-image{
    width: 12rem;
    padding: 0.25rem;
}

.product-title{
    font-size: 15px;
    font-weight: 500;
    color: $SECONDARY_COLOR;
    width: 100%;
    font-family: $SECONDARY_FONT;
}

.product-title:hover{
    color:$PRIMARY_THEME_COLOR;
    text-decoration: none;
}


.product-price{
    color: $PRIMARY_THEME_COLOR;
    font-family: $PRIMARY_FONT;
    font-size: 16px;
    font-weight: 600;
}

.sizes{
    font-size: 16px;
    font-weight: 600;
    color: $SECONDARY_COLOR;
}

.btn-view-product{
    border: none;
    border-radius: 5px;
    padding: 10px;
    font-family: $PRIMARY_FONT;
    width: 100%;
    border: 1px solid lightgray;
    color: $PRIMARY_COLOR;
    font-size: 15px;
}

.product-btn-container{
    height: min-content;
    display: flex;
    width: 100%;
    gap: 20px;
}


@media screen and (max-width: 576px) {
    .single-card{
        width: 10rem;
    }
    .product-image{
        width: 100%;
        padding: 0;
    }

    .product-title{
        font-size: 13px;
    }

    .product-price{
        font-size: 14px;
    }

    .btn-view-product{
        font-size: 12px;
        padding: 5px;
    }
}

/******************************************************************************************/
.product-screen-main-container{
    width: 100%;
    background-color: $PRIMARY_BG_COLOR;
    padding: 0.5rem;
}

.product-screen-container{
    padding: 0.25rem;
    width: 100%;
    background-color: white;
    border-radius: 5px;
}

.product-details-container{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.product-screen-img{
    width: 350px;
}

.product-detail-price{
    color: $PRIMARY_THEME_COLOR;
    font-size: 24px;
}

.product-quantity{
    font-size: 16px;
    font-family: $SECONDARY_FONT;
}

.single-product-view-quantity{
    width: 50px;
    padding: 5px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    color: $PRIMARY_COLOR;
    font-family: $SECONDARY_FONT;
}

.single-product-view-quantity:focus{
    outline: none;
}


.btn-add-cart{
    background-color: $PRIMARY_THEME_COLOR;
    border: none;
    color: white;
    padding: 10px;
    border-radius: 5px;
    font-size: 18px;
    align-items: center;
    width: 200px;
    text-transform: uppercase;
    font-family: $SECONDARY_FONT;
    font-weight: 500;
    letter-spacing: 2px;
}

.delivery-info{
    display: flex;
    align-items: center;
    gap: 10px;
}

.delivery-info > span {
    color: #008000;
    font-family: $SECONDARY_FONT;
    font-size: 15px;
}

.tag-icon{
    color: #008000;
}

.add-cart-container{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.available-sizes{
    width: 2rem;
    height: 2rem;
    align-items: center;
    display: flex;
    justify-content: center;
    border: 1px solid #EBEBEB;
}

.available-sizes > span{
    font-size: 16px;
    color: $PRIMARY_COLOR;
    font-family: $SECONDARY_FONT;
}

.product-Description{
    width: 400px;
    font-family: $SECONDARY_FONT;
    font-size: 14px;
    text-align: justify;
}

@media screen and (max-width: 576px) {
    .product-screen-img{
        width: 100%;
    }

    .btn-add-cart{
        width: 100%;
    }

    .product-Description{
        width: 100%;
    }
    
}
/*******************************************************************************************/
.similar-products-cotainer{
    display: flex;
    flex-direction: row;
    overflow-x: scroll;
    width: 100%;
}

.similar-product{
    width: 8rem;
}

.similar-product-img{
    width: 5rem;
}


