@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&family=Poppins&family=Roboto&display=swap');

$HEADER_TOP_BG: #FFFFFF;
$PRIMARY_COLOR: #232F3E;
$SECONDARY_COLOR: #37475A;

$PRIMARY_FONT: "Poppins", sans-serif;
$SECONDARY_FONT: "Roboto", sans-serif;

$PRIMARY_HEADER_COLOR: #FFFFFF;

$PRIMARY_THEME_COLOR: #1995D3;
$SECONDARY_THEME_COLOR: #1AB744;
$TRANSITION: all ease-in-out 0.3s;

.site-logo{
    text-decoration: none;
    font-size: 32px;
    font-weight: 800;
    color: $PRIMARY_THEME_COLOR;
}


.site-logo:hover{
    text-decoration: none;
    color: $PRIMARY_THEME_COLOR;
}

.main-top-header-container{
    border-bottom: 1px solid #EBEBEB;
    padding: 8px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 30;
    background-color: $PRIMARY_COLOR;
}

.header-links{
    color: lightgray;
    font-size: 13px;
    font-family: $SECONDARY_FONT;
    transition: $TRANSITION;
}

.header-links:hover{
    transition: $TRANSITION;
    color: white;
    text-decoration: none;
}

.menu-arrow{
    color: lightgray;
    font-size: 18px;
}

.top-header-container{
    background-color: $HEADER_TOP_BG;
    display: flex;
    padding: 0.75rem;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: sticky;
    top: 0;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.19);
}

.logo-container{
    display: flex;
}


.search-bar-container{
    display: flex;
    align-items: center;
}

.main-search-bar{
    width: 550px;
    cursor: pointer;
    border: none;
    border-radius: 5px 0 0 5px;
    background-color:white;
    padding: 8px;
    font-size: 15px;
    font-family: $PRIMARY_FONT;
    border: 1px solid #EBEBEB;
}

.main-search-bar:focus{
    outline: none;
}

.btn-search{
    padding: 8px;
    border: none;
    background-color: $SECONDARY_THEME_COLOR;
    color: white;
    border-radius: 0 5px 5px 0;
}

.signin-cart-container{
    display: flex;
    color: white;
    align-items: center;
    gap: 1rem;
    font-family: $PRIMARY_FONT;
}

.search-icon{
    display: none;
    font-size: 2rem;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
    color: $PRIMARY_THEME_COLOR;
}

.search-icon:hover{
    background-color: $SECONDARY_COLOR;
    color: white;
}

.shopping-cart{
    background-color: $PRIMARY_THEME_COLOR;
    padding: 10px;
    border-radius: 5px;
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5px;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.19);
}

.shopping-cart > span{
    text-decoration: none;
    color: white;
    font-size: 15px;
    font-family: $SECONDARY_FONT;
}

.cart-icon{
    color: white;
    font-size: 1.5rem;
}

.account-icon{
    font-size: 2.25rem;
    padding: 5px;
    border-radius: 5px;
    color: $PRIMARY_THEME_COLOR;
    transition: $TRANSITION;
}

 
.account-icon:hover{
    background-color: $SECONDARY_COLOR;
    color: white;
    transition: $TRANSITION;
}

.sign-links{
    font-family: $SECONDARY_FONT;
    padding: 5px 10px;
    font-size: 16px;
}

.sign-links:hover{
    background-color: $PRIMARY_COLOR;
    color: white;
    border-radius: 5px;
    text-decoration: none;
}

@media screen and (max-width:576px) {
    .site-logo{
        font-size: 22px;
    }

    .header-links{
        font-size: 11px;
    }

    .signin-cart-container{
        gap: 0;
    }
    .shopping-cart{
        padding: 4px;
    }
    .shopping-cart > span{
        font-size: 12px;
    }
    
}


@media screen and (max-width: 960px){
    .top-header-container{
        align-items: center;
    }

    .search-bar-container{
        display: none;
    }

    .search-icon{
        display: block;
        font-size: 1.75rem;
    }

}

.second-header-container{
    background-color: $PRIMARY_HEADER_COLOR;
    align-items: center;
    display: flex;
    padding: 8px;
    gap: 1rem;
    justify-content: start;
    border-top: 1px solid #EBEBEB;
    border-bottom: 1px solid #EBEBEB;
}

.menu-bar-container{
    display: flex;
    justify-content: start;
    color: white;
}
.menu-bar{
    font-size: 34px;
    color: $PRIMARY_COLOR;
    cursor: pointer;
    padding: 5px;
    transition: $TRANSITION;
    border-radius: 5px;
}

.menu-bar:hover{
    background-color: $SECONDARY_COLOR;
    transition: $TRANSITION;
    color: white;
}

.top-nav-data-container{
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.navi-item{
    padding: 5px 8px;
}
.navi-item:hover{
    border-radius: 5px;
    text-decoration: none;
    background-color: #EAEDED;
}

.nav-title{
    color: $PRIMARY_COLOR;
    font-size: 13px;
    font-family: $SECONDARY_FONT;
}

@media screen and (max-width: 1100px) {
    .top-nav-data-container{
        display: none;
    }
    
}

/*****************Sidebar****************************/

.nav-menu{
        display: flex;
        flex-direction: column;
        width: 30%;
        height: 100vh;
        position: fixed;
        left: -100%;
        transition: all 0.3s ease-out;
}

.nav-menu.active{
    left: 0;
    transition: all 0.5s ease-out;
    background: #ffffff;
    z-index: 1;
    top: 0;
    height: 100vh;
    overflow-y: scroll;
}

.sidebar-profile-container{
    position: fixed;
    height: 2rem;
    padding: 10px;
    width: auto; 
    background-color: $PRIMARY_COLOR;
}

.nav-item{
    padding: 10px;
    list-style-type: none;
}

.nav-item:hover{
    background-color: #EAEDED;
    cursor: pointer;
}

/*****************************************************************************************/
.show-searchbar{
    display: none;
    position: static;
    top: 0;
    width: 100%;
    background-color: white;
    padding: 10px;
}

.show-searchbar.search-active{
    display: flex;
    align-items: center;
    gap: 1rem;
    justify-content: space-between;
}

.close-icon{
    font-size: 2rem;
    color: gray;
    cursor: pointer;
}

@media screen and (max-width: 800px) {
    .main-search-bar{
        width: 100%;
    }
}